header {
    position: sticky;
    top: 0;
    background-color: black;
    text-align: end;
    z-index: 1000;
    img {
        display: flex;
        width: 240px;
        position: relative;
        top: 10px;
    }
    a {
        position: relative;
        bottom: 30px;
        padding: 10px;
        right: 20px;
        color: #6d89f0;
        text-decoration: none;
    }
}

a.underline {
    text-decoration: underline;
}